	module.exports = {"data":{"site":{"siteMetadata":{"title":"signalwerk"}},"markdownRemark":{"html":"<h1 id=\"open-sans\"><a href=\"#open-sans\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Open Sans</h1>\n<p><small>Natasha Ruf</small></p>\n<div class=\"col1to12\">\n<p><img src=\"/IAD2017.schriftmuster/OpenSans-ccabf04b85bfa08331201db8924ef0f2.svg\" alt=\"Open Sans\"></p>\n</div>\n<h2 id=\"legende\"><a href=\"#legende\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Legende</h2>\n<ol>\n<li>Oberkant Oberlänge überragt Versalhöhe</li>\n<li>Geschlossener Punzen</li>\n<li>Verkürzte, abgeschrägte Seitelänge beim m</li>\n<li>Geschlossener Punzen</li>\n<li>Geschlossener Punzen</li>\n<li>gerader Absatz</li>\n<li>abgeschrägter Absatz</li>\n</ol>\n<br>\n<h2 id=\"design\"><a href=\"#design\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Design</h2>\n<p><b>Open Sans wurde überall verwendet. Sie gehört zu den beliebtesten Google Fonts.</b></p>\n<p>Diese serifenlose, humanistische Schrift wurde von einem amerikanischen Schriftdesigner, Steve Matteson, im Auftrag von Google entworfen. Sie soll ein geradliniges, freundliches Aussehen haben und ist optimiert für eine gute Lesbarkeit auf Papier, Web und Mobile. Sie hat grosse Ähnlichkeit mit Droid Sans. Weil sie ein Ableger der Droid Sans ist. Diese Schrift ist merklich breiter und vielfältiger als Droid Sans und besitzt ganze 10 Schnitte. Sie verfügt über knapp 900 Glyphen mit einer Abdeckung der west- und osteuropäischen Zeichen sowie Kyrillisch und Griechisch.</p>\n<p>Open Sans wurde ab 2011 offiziell öffentlich freigegeben. Jeder kann sie von Google kostenlos herunterladen und verwenden. So kann man Google Fonts in Webseiten einbinden. In Microsoft Word ist Open Sans vorinstalliert.</p>\n<p>Open Sans ist bei Designern im Flat Design beliebt. Für die Standardschrift von Mozilla wurde dieser Schrift für Webseite und Desktop-App verwendet.</p>\n<p>Seit 2016 gibt es sogar auch eine serifenbetonte Schrift – Open Serif.</p>\n<br>\n<h2 id=\"designer\"><a href=\"#designer\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Designer</h2>\n<p><b>Steve Matteson</b></p>\n<p>Der amerikanische Schriftdesigner Steve Matteson ist im Jahr 1965 geboren und lebt zurzeit in Louisville (Colorado). Er studierte am Rochester Institute of Technology und war von den dort verwahrten Sammlungen historischer Ausrüstungen für den Typenguss und Buchdruck fasziniert. Seit 30 Jahren beschäftigt er sich mit Schriften in jeder Form. Er hat selbst viele gestaltete Schriften entworfen. Die Schriften – Droid, Open Sans usw stammen von ihm. Darüber hinaus entwarf er Schriften für Marken wie Microsoft und Google, e-Reader-Schriften für Amazon und Barnes &#x26; Noble. Ab 1991 wurde er bei Monotype tätig und arbeitet dort als Creative Type Director.</p>\n<p>Im Jahr  2005 verliess er das Unternehmen um die Astender Corporation zu gründen, wo er  mehr als 25 Schriftfamilien entwarf, einschließlich der Benutzerschnittstellen-Schriftarten für die Xbox und Googles mobile Android-Plattform.</p>\n<p>Im Jahr  2010 kehrte er als Creative Type Director zu Monotype zurück. Seitdem erarbeitet er Textschriften um das Lesen auf dem Bildschirm zu optimieren und Lesbarkeitsstudien zur Verbesserung der Sicherheit in der Automobil- und Luftfahrtindustrie zu unterstützen.</p>\n<p>Heute ist er ein berühmter Schriftdesigner geworden. Seine andere Leidenschaft ist die Musik und biken. </p>\n<br>\n<h4 id=\"quellen\"><a href=\"#quellen\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Quellen</h4>\n<ol>\n<li><a href=\"https://en.wikipedia.org/wiki/Open_Sans\">Open Sans im Wikipedia</a></li>\n<li><a href=\"https://www.typografie.info/3/Schriften/fonts.html/open-sans-r438/\">Open Sans im Typografie.info</a></li>\n<li><a href=\"http://www.linotype.com/de/3238/steve-matteson.html\">Steve Matteson im Linotype</a></li>\n<li><a href=\"http://en.wikipedia.org/wiki/Steve_Matteson\">Steve Matteson im Wikipedia</a></li>\n<li><a href=\"http://www.typoecon.com/speakers/steve-matteson\">Steve Matteson im Typecon</a></li>\n</ol>","frontmatter":{"date":"April 05, 2018","title":"Open Sans"}}},"pathContext":{"slug":"/fonts/natasha/"}}

